/*  Colors scheme  */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
  position: relative;
  width: 100%;
  color: #333333;
  font-size: 14px;
}

body {
  font-size: 100%;
  line-height: 150%;
  font-family: "PT Sans", sans-serif;
  background-color: #dbdbdb;
}

input,
button,
textarea,
select {
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 144%;
}

input::-ms-clear {
  display: none;
}

input[type=text],
input[type=email],
input[type=tel],
textarea,
fieldset {
  appearance: none;
  border: none;
  outline: none;
  margin: 0;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.button:hover,
.button:active,
.button:focus {
  outline: transparent;
}

a {
  cursor: pointer;
}

a,
a:visited,
a:focus,
a:active,
a:hover {
  text-decoration: none;
}

img {
  vertical-align: middle;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

:focus {
  outline: 0;
}

.landing-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  background-color: #ffffff;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: none;
  padding: 0 18px;
}
@media all and (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  font-size: 18px;
  font-weight: 700;
  line-height: 144%;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
}
@media all and (min-width: 768px) {
  .title {
    font-size: 21px;
    line-height: 124%;
  }
}
@media all and (min-width: 768px) {
  .title--large {
    font-size: 30px;
  }
}
.title--light {
  color: #ffffff;
}

.text {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 150%;
  margin: 0;
}
.text--light {
  color: #ffffff;
}

@media all and (min-width: 768px) {
  .text-wrap {
    display: block;
  }
}

.header {
  position: relative;
}

.header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.header__nav {
  display: grid;
}

.nav {
  height: 56px;
  background-color: #ffffff;
}
@media all and (min-width: 768px) {
  .nav {
    min-height: 56px;
    height: auto;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  display: none;
}
@media all and (min-width: 768px) {
  .nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.nav__list--open {
  display: block;
  z-index: 2;
}

.nav__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 55px;
  padding: 15px;
  background-color: #81b3d2;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  transition: 0.3s ease-out;
}
@media all and (max-width: 767px) {
  .nav__item:focus-within {
    background-color: #669ec0;
    transition: 0.3s ease-out;
  }
  .nav__item:active {
    background-color: #5496bd;
    transition: 0.3s ease-out;
  }
  .nav__item .nav__link:active {
    color: rgba(255, 255, 255, 0.3);
    transition: 0.3s ease-out;
  }
}
@media all and (min-width: 768px) {
  .nav__item {
    background-color: #ffffff;
    box-shadow: none;
    width: 20%;
  }
  .nav__item:last-child::after {
    display: none;
  }
  .nav__item:nth-child(3) {
    margin: 0 0 0 20%;
  }
}
.nav__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}

.nav__link {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 144%;
  color: #ffffff;
  text-transform: uppercase;
  outline: none;
  transition: 0.3s ease-out;
}
@media all and (min-width: 768px) {
  .nav__link {
    color: #000000;
    font-size: 14px;
  }
  .nav__link:hover, .nav__link:focus {
    color: #81b3d2;
    transition: 0.3s ease-out;
  }
  .nav__link:active {
    color: rgba(0, 0, 0, 0.3);
    transition: 0.3s ease-out;
  }
  .nav__link--active {
    color: #766357;
    transition: 0.3s ease-out;
  }
}

@media all and (max-width: 767px) {
  .nav.nav--nojs {
    height: auto;
  }
  .nav.nav--nojs .nav__list {
    display: block;
    z-index: 1;
  }
  .nav.nav--nojs .nav__button {
    display: none;
  }
}

.nav-button {
  position: absolute;
  width: 23px;
  height: 23px;
  top: 50%;
  right: 20px;
  border: none;
  background: transparent;
  transform: translateY(-50%);
  transition: 0.3s;
  -webkit-tap-highlight-color: transparent;
}
@media all and (min-width: 768px) {
  .nav-button {
    display: none;
  }
}
.nav-button:focus {
  opacity: 0.6;
}
.nav-button:active {
  opacity: 0.3;
}
.nav-button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.nav-button::after {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2;
}
.nav-button--close {
  transition: 0.3s;
  z-index: 3;
}
.nav-button--close::before {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}
.nav-button--close::after {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(45deg);
  box-shadow: none;
}

