.gallery-card {
  position: relative;
  padding: 25px 20px 0;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;

  @include md-width {
    padding: 0;
    border-top: none;
    border-bottom: none;
  }

  &--wide {
    @include md-width {
      display: grid;
      width: 100%;
      grid-row-start: 1;
      grid-column-start: 1;
      grid-row-end: 2;
      grid-column-end: 3;
    }

    @include lg-width {
      grid-column-end: 4;
    }
  }
}

.gallery-card__subtitle,
.gallery-card__button {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;

  @include md-width {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 200px;
  }

  @include lg-width {
    max-width: 280px;
  }
}

.gallery-card__img {
  width: 100%;
  height: auto;
}

.gallery-card__content {
  padding: 14px 0 25px;

  @include md-width {
    background-color: $form-bg-clr;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 15px 17px 20px;
  }

  @include lg-width {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 22px 15px 18px 25px;
  }
}

.gallery-card__description {
  color: $text-clr;

  @include md-width {
    display: flex;
    flex-direction: column;
  }

  @include lg-width {
    color: $light-clr;
  }
}

.gallery-card__author {
  font-size: 14px;
  font-weight: 400;
  line-height: inherit;

  @include md-width {
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 200px;
  }

  @include lg-width {
    max-width: 280px;
  }
}

.gallery-card__button {
  display: block;
  margin-top: 13px;
  border: none;
  min-height: 25px;

  @include md-width {
    margin-top: 0;
    min-height: 30px;
  }
}
