/*  Colors scheme  */
$primary-clr: #81b3d2;
$primary-hover: #669ec0;
$primary-down: #5496bd;
$secondary-clr: #eeeeee;
$light-clr: #ffffff;
$dark-clr: #000000;
$text-clr: #333333;
$text-current: #766357;
